.form {
  &__password-container {
    margin-top: 20px;
  }
  .password {
    &__initial {
      &-inner {
        display: flex;
        align-items: center;
        @media screen and (max-width: 420px) {
          flex-direction: column;
          align-items: flex-start;
        }
        p {
          display: inline-block;
          margin-left: 10px;
          font-size: 12px;
          color: #999;
          @media screen and (max-width: 420px) {
            margin: 10px 0 0 0;
          }
        }
      }
    }
    &__confirm {
      margin-top: 20px;
      &-inner {
        display: flex;
        align-items: center;
        @media screen and (max-width: 420px) {
          flex-direction: column;
          align-items: flex-start;
        }
        p {
          display: inline-block;
          margin-left: 10px;
          font-size: 12px;
          color: #999;
          @media screen and (max-width: 420px) {
            margin: 10px 0 0 0;
          }
        }
      }
    }
    &__field {
      display: block;
      padding: 10px;
      border: none;
      border: 1px solid #ddd;
      border-radius: 15px;
      margin-top: 10px;
    }
    &__error-caption {
      margin-top: 5px;
      font-size: 14px;
      color: #ff0000;
    }
  }
  .input-error {
    border: 2px solid #ff0000;
  }
}
