.form {
  &__email-container {
    margin-top: 20px;
  }
  .email {
    &__inner {
      display: flex;
      align-items: center;
      @media screen and (max-width: 576px) {
        flex-direction: column;
        align-items: flex-start;
      }
      p {
        color: #999;
        font-size: 12px;
        margin-left: 10px;
        @media screen and (max-width: 576px) {
          margin: 10px 0 0 0;
        }
      }
    }
    &__field {
      display: block;
      padding: 10px;
      border: none;
      border: 1px solid #ddd;
      border-radius: 15px;
      margin-top: 10px;
      font-size: 16px;
      width: 310px;
      font-family: "GTEestiProText Regular", Arial, Helvetica, sans-serif;
      @media screen and (max-width: 375px) {
        width: 280px;
      }
    }
  }
  .email {
    &__error-caption {
      margin-top: 5px;
      font-size: 14px;
      color: #ff0000;
    }
  }
  .input-error {
    border: 2px solid #ff0000;
  }
}
